<template>
    <!-- ==== PORTFOLIO ==== -->
  <section class="portifolio" id="portfólio">
    <div class="about-header">
      <h2>Portfólio</h2>
    </div>
    <div class="portifolio-content container" >
      <!-- ==== SINGLE PORTFOLIO ==== -->
      <article v-for="posts of posts" :key="posts.id">
        <div class="portifolio-image">
          <img v-bind:src="posts.cover" v-bind:alt="posts.title" />
        </div>
        <div class="portifolio-body">
          <h2>{{ posts.title }}</h2>
          <p>{{ posts.subtitle }}</p>
          <a v-bind:href="posts.uri" target="_blank" v-bind:alt="posts.title">
            <i class="fas fa-door-open"></i> Visitar Website
          </a>
        </div>
        <div class="article-footer"></div>
      </article>
    </div>
  </section>
</template>

<script>
import Posts from "./../services/posts";
var date = new Date();
export default {
  data() {
    return {
      posts: [],
      year: date.getFullYear() 
    };
  },

  mounted() {
    this.getPosts();
  },

  methods: {
    getPosts() {
      Posts.getPosts().then((res) => {
        this.posts = res.data;
      });
    },
  },
};
</script>